

















































































  import rewardRule from '../../components/rewardRule.vue'
  
	export default {
    components: {
      rewardRule
    },
		data() {
			return {
        advLists: [],
				recommend: [
          {
            title: '新手办卡',
            tag: '办好卡 送好礼',
            url: '/static/images/credit/card-new.png',
            id: 1
          },
          {
          	title: '大额度',
          	tag: '额度任性带你飞',
          	url: '/static/images/credit/card-high.png',
          	id: 2
          },
          {
          	title: '审批快',
          	tag: '至快当天下卡',
          	url: '/static/images/credit/card-fast.png',
          	id: 3
          },
          {
          	title: '易下卡',
          	tag: '门槛低 易通过',
          	url: '/static/images/credit/card-easy.png',
          	id: 4
          }
        ],
        bankLists: [],
        cardLists: [],
        rewardData: {}
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      }
    },
		onLoad() {
      // banner
      this.$http({
        url: this.$apis.adverts,
        method: 'GET',
        data: {
          place: 2
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.advLists = data.data
          }
        }
      })
      // 银行列表
      this.$http({
        url: this.$apis.banks,
        method: 'GET',
        data: null,
        cb: (err, data) => {
          if (!err && data.success) {
            this.bankLists = data.data
          }
        }
      })
      // 热门信用卡
      this.$http({
        url: this.$apis.card,
        method: 'GET',
        data: {
          ishot: 1
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.cardLists = data.data
            this.cardLists.forEach(item => {
            	this.$set(item, 'tagArr', this.handleStr(item.show_tags))
            })
          }
        }
      })
		},
    methods: {
      handleStr(str) {
      	return str.split(',')
      },
      gotoRecommend(item) {
        this.$navigateTo({
        	url: '/pages/credit/recommend?id=' + item.id + '&title=' + item.title
        })
      },
      gotoBank(item) {
        if (item.isopen === 2) {
          uni.showToast({
          	title: '该银行即将上线',
            icon: 'none'
          })
          return
        }
        this.$navigateTo({
        	url: './bank?bankId=' + item.id + '&bankCode=' + item.code
        })
      },
      gotoQuery() {
        this.$navigateTo({
        	url: '/pages/credit/query'
        })
      },
      gotoCard(id) {
        this.$navigateTo({
        	url: '/pages/credit/card?id=' + id
        })
      },
      showRule(item) {
        this.rewardData = {
          show: true,
          total: item.earn,
          conditionTitle: item.clearing_title,
          conditionDec: item.clearing_info
        }
        this.changeBank = item
      },
      gotoHandle: function () {
        uni.setStorageSync('createCredit', true)
        this.$navigateTo({
        	url: './bank?bankId=' + this.changeBank.id + '&bankCode=' + this.changeBank.code
        })
      }
    }
	}
